

.items {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    flex-wrap: wrap;

    img {
        display: block;
        width: 80%;
        margin: auto;
    }

}


.item__card {
    display: flex;
    flex-direction: column;
    flex-basis: 24%;
    padding: 20px 30px;
    border: 2px solid #F3F3F3;
    border-radius: 20px;
    transition: all .3s ease-in-out;
    position: relative;
    margin: 5px;
    &:hover {
        box-shadow: 0 0 10px #cecdcd;
        transform: translateY(-10px);
    }
    .checked{
        background: url(../../../public/img/btn-checked.svg) center/ cover;
    }
    button{
        transition: all .3s ease-in-out;
    }
   

    .heart__icon {
        width: 32px;
        height: 32px;
        position: absolute;
        background: url(../../../public/img/favorite-unchecked.svg) no-repeat center;
        top: 15%;
        left: 15%;
        border-radius: 50%;
        transition: all .3s ease-in-out;
        cursor: pointer;

        &:hover {
            box-shadow: 0 0 10px #FF8585;
        }
    }
    .onFavorite{
        background: url(../../../public/img/favorite-checked.svg) no-repeat center;
    
    }

    div {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
    }

    h3 {
        margin: 15px 0 10px 0;
    }

    p {
        font: .8rem;
        color: #BDBDBD;
    }
    

    button {
        padding: 15px;
        border: 0;
        border-radius: 8px;
        cursor: pointer;
        font-weight: 600;
        font-size: 1.2rem;
        color: #BDBDBD;
        background: url(../../../public/img/btn-unchecked.svg) center/ cover;

        &:hover {
            box-shadow: 0 0 10px #d8d8d8;

        }
    }

}



.price {
    font-weight: bold;
    font-size: 1.1rem;
    color: #000;
}

@media only screen and (max-width:910px) {

    .item__card{
        flex-basis: calc(50% - 30px);
    }
    
}
@media only screen and (max-width: 570px) {
    p{
        font-size: .9rem;
    }
    span{
    font-size: .8rem !important;
  }
  .heart__icon{
    top:5% !important;
    left: 5% !important;
  }

}
@media only screen and (max-width:450px) {

    .item__card{
        flex-basis: 100%;
    }
    
}
