
.header__search {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin: 20px 0;
}

.search__block {
    display: flex;
    justify-content: space-between;
    padding: 10px 15px;
    border: 1px solid #BDBDBD;
    border-radius: 10px;

    input {
        font-size: 1rem;
        border: 0;
        outline: 0;
        width: 100%;
        margin: 0 0 0 5px;
    }
}

.search__btn {
    border: 0;
    cursor: pointer;
}

.clear__input{
    margin: 0 0 0 5px;
    background: url(../../public/img/delete-cart.svg) center/cover;
    border: 1px solid #BDBDBD;
    cursor: pointer;
    padding: 5px 10px;
    border-radius: 50%;
}

@media only screen and (max-width: 910px) {
    .search__block{
        flex-basis: 50%;
    }
}
@media only screen and (max-width: 570px) {
    .search__block{
        flex-basis: 40%;
    }
}
@media only screen and (max-width: 450px) {
    .header__search{
        flex-direction: column;
    }
}



