#appContainer{
  background-color: #E7F6FF;
  min-height: 100vh;
  padding: 85px;
  position: relative;
}
#content{
  padding: 43px 45px;
  background-color: #fff;
  border-radius: 25px;
}


@media only screen and (max-width: 768px) {
  #appContainer{
    padding: 15px;
  }
}

@media only screen and (max-width: 450px) {
  #content{
    padding: 20px 10px;
  }
}


