.main__btn{
    display: block;
    width: 80%;
    margin: 10px auto;
    border: 0;
    padding: 20px;
    background-color: #9DD458;
    color: #fff;
    font: bold 1rem Inter;
    cursor: pointer;
    border-radius: 20px;
    &:disabled{
        background-color: #bebebe;
        animation-name: disabledBtn;
        animation-duration: .7s;
        animation-timing-function: ease-in-out;
        animation-iteration-count: infinite;
        cursor: default;
    }
}

@keyframes disabledBtn {
    0%{
        opacity: 1;
    }
    50%{
        opacity: .7;
    }
    100%{
        opacity: 1;
    }
}