


.cart__item {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    position: relative;
    margin: 10px auto;
    align-items: center;
    padding: 20px;
    border: 1px solid #BDBDBD;
    border-radius: 20px;

    img {
        width: 80%;
        display: block;
        margin: auto;
    }

    h4 {
        margin: 10px 0 0 0;
    }
}

.delete__cart {
    padding: 14px;
    border: 0;
    outline: 0;
    cursor: pointer;
    background: url(../../../public/img/delete-cart.svg) center/cover;
    position: absolute;
    right: 10px;
    top: 5px;
}