.item{
    display: flex;
    flex-direction: column;
    justify-content: center;
    flex-basis: 15%;
    margin: 0 5px;
    border: 1px solid #bebebeb6;
    padding: 5px;
    border-radius: 15px;
    align-items: center;
    box-shadow: 0 3px 10px #bebebe;
}

@media only screen and (max-width:450px) {
    .item{
        flex-basis: 90%;
    }
}