header,
.logo__name,
.nav__items,
.basket__summ,
.favorit__items,
.profile {
    display: flex;
    flex-direction: row;
    align-items: center;
}

header {
    justify-content: space-between;
}

.nav__items {
    img {
        width: 20px;
    }

    div {
        margin: 0 0 0 32px;
        cursor: pointer;
        a{
            display: flex;
        }
    }

    p {
        margin: 0 0 0 8px;
    }
}

.logo__name {
    div {
        margin: 0 0 0 16px;
    }
}
.burgerMenu{
    display: none;
}

@media only screen and (max-width:910px) {
    header {
        flex-direction: column;
    }
    .nav__items{
        div{
            margin: 20px;
        }
    }
}

@media only screen and (max-width:450px) {
   
    .burgerMenu{
        position: absolute;
        right: -35px;
        top: 5px;
        display: inherit;
        width: 30px;
        height: 30px;
        border: 0;
        padding: 0;
        overflow: hidden;
       
    }
    .sticks{
        width: 100%;
        position: relative;
        height: 100%;
        border: 2px solid #000000d3;
        background-color: #fff;
        border-radius: 4px;
        transition: all .5s ease-in-out;
        span:first-child{
            top: 20%;
            
        } 
        span:last-child{
            top:  80%;
        }
    }
    .stick{
        display: block;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        width: 90%;
        height: 3px;
        border-radius: 20px;
        background: #000000d3;
        transition: all 1s ease-in-out, background .5s ease-in-out;
    }

    .burgerClose{
        border-color: rgba(255, 0, 0, 0.712);
        .stick:nth-child(1){
            top: 50%;
            transform: translate(-50%, -50%)  rotate(45deg);
            background-color: rgba(255, 0, 0, 0.712);
        }
        .stick:nth-child(2){
            left: -100%;
            background-color: rgba(255, 0, 0, 0.712);
        }
        .stick:nth-child(3){
            top: 50%;
            transform: translate(-50%, -50%)  rotate(-45deg);
            background-color: rgba(255, 0, 0, 0.712);
        }
    }
    .header__nav {
        position: fixed;
        top: 0;
        display: block;
        left: -100%;
        z-index: 5;
        transition: all 1s ease-in-out;
        max-width: 165px;
        
    }
  
    .nav__items{
        flex-direction: column;
        align-items: flex-start;
        padding: 15px;
        margin: 0;
        background-color: rgba(255, 255, 255, 0.795);
        border-radius: 0  0 20px 0 ;
        box-shadow: 0 0 10px #000;
        div{
            margin: 20px;
        }
    }
    .burgerHidden{
        left: -165px;
    }
    .visible{
        left: 0;
    }
}