.contain{
    display: flex;
    flex-direction: row;
    position: relative;
    border: 1px solid #bebebe;
    padding: 20px;
    border-radius: 10px;
    align-items: center;
    box-shadow: 0 0 10px #bebebe;
    margin: 50px 0;
    > div{
        margin: 0 5px;
    }
    
}
.raport__time{
    position: absolute;
    right: 10px;
    top: -45px;
    padding: 5px 15px;
    box-shadow: 0 -3px 10px #bebebe;
    border-radius: 15px 15px 0 0;
    p{
        font-size: .7rem;
        font-weight: 600;
    }
   
}
.state__contain{
    text-align: center;
    flex-basis: 10%;
}
.state{
    border: 2px solid #add5ed;
    border-radius: 20px;
    font-size: .9rem;
    box-shadow: 0 0 10px #add5ed inset;
}
.items{
    display: flex;
    flex-direction: row;
    flex: 1;
    overflow-x: auto;
    text-align: center;
    padding: 0 5px;
}
.item__raport{
    border: 2px solid #bebebe;
    flex-basis: 35%;
    padding:35px 30px;
    border-radius: 15px;
    box-shadow: 0 0 10px #bebebe, 0 0 10px rgb(139, 139, 139) inset;
}

.order__road{
    border: 1px solid #7c7c7c;
    position: relative;
    margin: 10px 0;
    height: 10px;
    border-left: 0;
    border-right:0 ;
    img{
        width: 32px;
        height: 32px;
    }
}
.loading{
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    background-color: #b0d65f;
    transition: all .5s ease-in-out;
}

.point{
    position: absolute;
    padding: 10px;
    left: 0;
    transform: translateX(-50%);
    z-index: 4;
    &::after{
        content: "";
        position: absolute;
        top: -1px;
        left: 50%;
        width: 8px;
        height: 8px;
        border-radius: 50%;
        background-color: #78ad0762;
        transform: translate(-50%, 0);
        border: 1px solid #8a8484;
    }
    &:first-of-type::after{
        background-color: #78ad07;
    }

}
.passed::after{
    background-color: #78ad07;
}


@media only screen and (max-width:910px) {
    .contain{
        flex-direction: column;
        padding: 10px;
        > div{
            margin: 5px 0;
        }
    }
    .item__raport{
        width: 100%;
    }
    .items{
        flex-wrap: wrap;
        >div{
            margin: 5px;
        }
    }
    h4{
        margin: 5px 0;
    }
    h5{
        margin: 20px 10px;
    }

   
}
@media only screen and (max-width:450px) {
    .items{
        justify-content: center;
    }
    
}
