.cart__wrapper {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background: #00000062;
    height: 100%;
    z-index: 5;
    justify-content: end;
    backdrop-filter: blur(3px);
}
.cart__bg{
    position: relative;
    display: flex;
    align-items: end;
    flex-direction: column;
}

.cart {
    right: 0;
    width: 30%;
    background-color: #fff;
    padding: 30px 30px 0 30px;
    display: flex;
    flex-direction: column;
    position: relative;
    justify-content: space-between;
    height: 100vh;
}

.cart__items {
    display: flex;
    flex-direction: column;
    overflow: auto;
}


.cart__info{
   background-color: #fff;
   padding: 15px 5px 5px 5px;
 
}

.close__btn{
    position: absolute;
    right: 10px;
    top: 10px;
    padding: 18px;
    background: url(../../../public/img/delete-cart.svg) center/cover;
    border: 0;
    cursor: pointer;
}

.cart__sum {
    li {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        margin: 20px 0 0 0;
        align-items: end;
    }

}

.sum {
    font-weight: bold;
}

.hr {
    border-bottom: 1px dotted #BDBDBD;
    height: 100%;
    flex: 1;
    margin: 0 10px;
}

@media only screen and (max-width:910px) {
    .cart{
        width: 60%;
    }
}
@media only screen and (max-width:450px) {
    .cart{
        width: 100%;
    }
}